
@import '~antd/dist/antd.css';

html,
body,
.app {
  width: 100%;
  height: 100%;
  margin: 0;
  padding: 0;
  font-family: Tahoma, Geneva, sans-serif;
  color: #4b4f56;
}

.nav {
  border: 1px solid blue;
}

.content {
  border: 1px solid black;
  height: 600px;
}

.wrapper {
  position: relative;
  height: 100%;
  width: 100%;
}

.testGrid {
  border: 1px solid green;
  height: 100%;
  width: 100%;
  overflow: auto;
}